import { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import Card from '@mui/material/Card';
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import { Button, CardMedia, Typography } from '@mui/material';
import mainImg from './images/main-2.jpeg'
import axios from 'axios';

interface ISponsor {
  "nombre": string;
  "apellidos": string;
}

const Nuevo = () => {

    const [ sponsor, setSponsor ] = useState<ISponsor[]>([])
    const [ idRef, setIdRef ] = useState("")
    const { search } = useLocation()

    const query = new URLSearchParams(search)
    const ref = query.get("Ref")

    const getSponsor = async () => {
      if(!idRef) return
      console.log(idRef)
      const res = await axios.get('/api/sponsor/' + idRef)
      const da = await res.data
      setSponsor(da)
    }

    useEffect(() => {
      const r = window.localStorage.getItem("IdRef") 
      if(!r) {
        setIdRef(ref || "")
        ref && window.localStorage.setItem("IdRef", ref)
      }else{
        setIdRef(r)
      }
    }, [])

    useEffect(() => {
      getSponsor()
    }, [idRef])

    return (
      <Container sx={{ pt:12 }}>
        <Grid container spacing={4} justifyContent="center">
          <Grid item xs={12} sm={6} md={4}>
            <Card
              sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
            >
              <CardMedia
                  component="img"
                  height="300"
                  image={ mainImg }
                  alt="Finanservs"
              />
              <Link to="/registro">
                <Button variant="contained" >
                  Resgistro
                </Button>
              </Link>
            </Card>
          </Grid>
        </Grid>
        <Typography sx={{ pt:2 }} textAlign="center">
          { sponsor.length > 0 && `Referido por: ${sponsor[0].nombre} ${sponsor[0].apellidos}`}
        </Typography>
      </Container>
    )
}

export default Nuevo;