import { useEffect, useState } from 'react';
import { useLocation } from 'react-router';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import axios from 'axios';
import { Divider, ListItemText } from '@mui/material';

interface IAfiliados {
    id: string;
    nombre: string;
    apellidos: string;
    email: string;
    celular: string;
    dateCreated: string;
}

export const Afiliados = () => {

    const [ afiliados, setAfiliados ] = useState<IAfiliados[]>([])
    const { search } = useLocation()

    const query = new URLSearchParams(search)
    const idRef = query.get("idRef")

    const getAfiliados = async () => {
        const res = await axios.get('/api/afiliados/' + idRef)
        const da = await res.data
        setAfiliados(da)
    }

    useEffect(() => {
        getAfiliados()
    }, [])


    return (
        <Container sx={{ pt: 8 }} maxWidth="md">
        <Typography  variant="h5" textAlign="center">
        Link de Referidos: 
        </Typography>
        <Typography textAlign="center">
            { `https://network.finanservs.com?Ref=${"1"}` }
        </Typography>
        <Divider sx={{ pb: 0 }} />
        <Typography gutterBottom sx={{ pt: 2 }} variant="h4" component="h2" textAlign="center">
            Mis Referidos
        </Typography>
            {afiliados ?
            (
            <Grid container spacing={2}>
              {afiliados.map((afil) => (
                <Grid item key={afil.id} xs={12} sm={6} md={4}>
                  <Card
                    sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                  >
                    <CardContent sx={{ flexGrow: 1 }}>
                      <Typography gutterBottom variant="h5" component="h2">
                        {afil.nombre + ' ' + afil.apellidos}
                      </Typography>
                      <Typography>
                        <ListItemText primary={`Email: ${afil.email}`} />
                        <ListItemText primary={`Celular: ${afil.celular}`} />
                        <ListItemText primary={`Creado: ${afil.dateCreated}`} />
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              ))}
            </Grid>
            )
            : 
            (
                <Grid item  xs={12} sm={6} md={4}>
                <Card
                  sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
                >
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h5" component="h2">
                      Sin Referidos
                    </Typography>
                    <Typography>
                      No deje de compartir con sus amigos esta oportunidad.
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>   
            )
            }
        </Container>
    )
}

export default Afiliados;