import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import axios from 'axios';
import AlertMessage from './AlertMessage';

const theme = createTheme();

export default function SignIn() {
    let navegate = useNavigate();
    const [errorMessage, setErrorMessage] = useState("")

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const form = new FormData(event.currentTarget);
    // eslint-disable-next-line no-console
    const data = {
      email: form.get('email'),
      password: form.get('password'),
    }

    try {
        const result = await axios.post('/api/login/', data)
        const da = await result.data
        if(da.token) {
            const id = da.idUser
            const daUser = {
                token: da.token,
                idUser: da.idUser,
                idRef: da.id_ref,
                email: da.email,
            }
            window.localStorage.setItem('jwt', JSON.stringify(daUser));
            navegate(`/afiliados/?idRef=${id}`)
        } else {
            setErrorMessage(da.message)    
        }
    } catch( ex ) {
        setErrorMessage("Error: Query no permitido. Favor ver Log del Servidor.")
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Inicio
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email"
              name="email"
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Contraseña"
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Recuerdame"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Inicio
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="/afiliados" variant="body2">
                  Olvido su Contraseña?
                </Link>
              </Grid>
              <Grid item>
                <Link href="/registro" variant="body2">
                <Typography>
                    Resgistrarse
                </Typography>
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        { errorMessage ? 
        <AlertMessage 
          type={"warning"}
          message={errorMessage} 
          setMsg={setErrorMessage}
        />
        : ""
        }
      </Container>
    </ThemeProvider>
  );
}